<template>
  
  <v-stepper v-model="currentStep">
    <camera-dialog @captureClose="onCaptureClose" @cancelClose="onCancelClose"></camera-dialog>
    <modal-dialog></modal-dialog>


<!--     <v-stepper-header>
      <template v-for="n in steps">
        <v-stepper-step
          :complete="currentStep > n"
          :key="`${n}-step`"
          :step="n"
        >
          Guest No.{{ n }}
        </v-stepper-step>

        <v-divider
          v-if="n !== steps"
          :key="n"
        ></v-divider>
      </template>
    </v-stepper-header> -->

    <v-stepper-items>
      <v-stepper-content
        v-for="n in steps"
        :key="`${n}-content`"
        :step="n"
      >
        <v-layout row fill-height>
          <v-flex xs6>
            <span class="headline"><v-btn class="headline" flat @click="showCameraDialog(true)">{{ $t("checkIn.titleId") }}</v-btn></span>
          </v-flex>
          <v-flex xs6>
            <span class="headline"><v-btn class="headline" flat @click="showCameraDialog(false)">{{ $t("checkIn.titleFace") }}</v-btn></span>
          </v-flex>
        </v-layout>  
        <v-layout row fill-height>
          <v-flex xs12>
            <v-container fluid>
              <v-layout row justify-space-between>
                <v-flex xs6>
                  <img :src="idImgSrc" class="img-responsive" width="80%" @click="showCameraDialog(true)" style="cursor: pointer;"/>
                </v-flex>
                <v-flex xs6>
                  <img :src="faceImgSrc" class="img-responsive" width="80%" @click="showCameraDialog(false)" style="cursor: pointer;"/>
                </v-flex>
              </v-layout>
            </v-container>
          </v-flex>
        </v-layout>

        <v-layout row fill-height>
          <v-flex xs12>
            <v-container fluid>
              <v-layout row justify-space-between>
                <v-flex xs3>
                  <v-text-field label="*First name" required hint="*" v-model="guest.firstName" v-if="guest" ></v-text-field>
                </v-flex>
<!--                 <v-flex xs3>
                  <v-text-field label="Middle name"></v-text-field> 
                </v-flex> -->
                <v-flex xs3>
                  <v-text-field label="*Last name" required hint="*" v-model="guest.lastName" v-if="guest" ></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
            <small>*indicates required field</small>
          </v-flex>
        </v-layout>

        <v-layout row fill-height>
          <v-flex xs12>
            <v-spacer></v-spacer>
            <!-- <v-btn color="blue darken-2" flat @click="showDialog(false)">Take Picture</v-btn> -->
            <v-btn class="headline" icon @click="prevStep(n)">
              <v-icon x-large>navigate_before</v-icon>
            </v-btn>
            <v-btn class="headline" flat @click="prevStep(n)">{{ $t("checkIn.previous") }}</v-btn>
            <!-- <v-btn class="headline" flat @click="verify()">{{ $t("checkIn.verifyId") }}</v-btn> -->
            <!-- <v-btn class="headline" flat @click="nextStep(n)">{{ $t("checkIn.next") }}</v-btn> -->
            <span class="headline">{{ currentStep }} / {{ steps }}</span>
            <v-btn class="headline" flat @click="goNext()">{{ $t("checkIn.next") }}</v-btn>
            <v-btn class="headline" icon @click="goNext()">
              <v-icon x-large>navigate_next</v-icon>
            </v-btn>
          </v-flex>
        </v-layout>  

      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
</template>

<script>
import CameraDialog from '@/components/CameraDialog.vue'
import ModalDialog from '@/components/ModalDialog.vue'
import { EventBus } from '@/eventBus'
import { Storage } from 'aws-amplify'
import aws_exports from '@/aws-exports'
import { mapState, mapGetters, mapActions } from 'vuex'


export default {
  name: 'CheckIn',
  data () {
    return {}
  },
  created () {
    // console.log(JSON.stringify(this.guests))
    this.reloadGuests()
    this.setCurrentStep(1)
  },
  computed: {
    currentStep: {
      get() {
        return this.$store.state.checkIn.currentStep
      },
      set (val) {
        this.setCurrentStep(val)
      }
    },
    ...mapState({
      userIdentityId: state => state.amplify.userIdentityId,
      guests: state => state.checkIn.guests,
      faceImgSrc: state => state.checkIn.faceImgSrc,
      idImgSrc: state => state.checkIn.idImgSrc
    }),
    ...mapGetters('checkIn', {
      steps: 'steps',
      guest: 'guest',
      reservation: 'reservation'
    })
  },
  watch: {
    guest(newVal, oldVal) {
      // console.log('guest watch oldVal', JSON.stringify(oldVal))
      // console.log('guest watch newVal', JSON.stringify(newVal))
      // console.log(JSON.stringify(this.guests))
    }
  },
  methods: {
    prevStep (n) {
      if (n === 1) {
        this.setCurrentStep(this.steps)

      } else {
        this.setCurrentStep(n - 1)
      }
    },    
    nextStep (n) {
      if (n === this.steps) {
        this.setCurrentStep(1)
      } else {
        this.setCurrentStep(n + 1)
      }
    },
    async goNext() {
      // console.log('guest', JSON.stringify(this.guest))
      if (this.reservation.idVerified) {
        if (!this.guest.passed) {
          if (this.guest.imgBucket && this.guest.idImgKey && this.guest.faceImgKey) {
            await this.verifyId()
            console.log(`similarity: ${this.guest.similarity}\nmessageCode: ${this.guest.messageCode}`)
          }
        }
        // this.inputGuest(savedGuest)
      }

      await this.saveGuest()
      const rtn = await this.saveReservation()
      if (rtn) {
        this.showModalDialog()
      }

      console.log('rtn', JSON.stringify(rtn))

      if (this.currentStep === this.steps) {
        this.setCurrentStep(1)
      } else {
        this.nextStep(this.currentStep)  
      }
    },
    showCameraDialog(isId) {
      EventBus.$emit('CameraDialog', { showDialog: true, isId });
      this.isId = isId;
    },
    showModalDialog() {
      EventBus.$emit('ModalDialog', { showDialog: true });
    },
    onCancelClose() {
      console.log('cancelClose')
    },
    async onCaptureClose(val) {
      if (val.base64Img) {
        if (val.isId) {
          await this.putImage({img: val.base64Img, isId: true})
          await this.setIdImgSrc()
        } else {
          await this.putImage({img: val.base64Img, isId: false})
          await this.setFaceImgSrc()
        }
      }
    },
    ...mapActions({
      reloadGuests: 'checkIn/reloadGuests',
      setCurrentStep: 'checkIn/setCurrentStep',
      setIdImgSrc: 'checkIn/setIdImgSrc',
      setFaceImgSrc: 'checkIn/setFaceImgSrc',
      putImage: 'checkIn/putImage',
      verifyId: 'checkIn/verifyId',
      saveGuest: 'checkIn/saveGuest',
      saveReservation: 'checkIn/saveReservation'
    })
  },
  components: {
    cameraDialog: CameraDialog,
    ModalDialog: ModalDialog
  }
}
</script>