import appsync from '@/api/appsync'
import storage from '@/api/storage'
import aws_exports from '@/aws-exports'

const state = {
  reservation: null,
  guests: [],
  currentStep: 1,
  idImgSrc: null,
  faceImgSrc: null,
  noPriorAccessAlert: false
}

const mutations = {
  setReservation(state, val) {
    state.reservation = val
  },
  setGuests(state, val) {
    state.guests = val
  },
  setCurrentStep(state, val) {
    state.currentStep = val
  },
  setIdImgSrc(state, val) {
    state.idImgSrc = val
  },
  setFaceImgSrc(state, val) {
    state.faceImgSrc = val
  },
  setNoPriorAccessAlert(state, val) {
    console.log('setNoPriorAccessAlert', val)
    state.noPriorAccessAlert = val
  }
}

const actions = {
  setCheckInDone ({ commit }, val) {
    commit('setNoPriorAccessAlert', val)
  },
  setNoPriorAccessAlert ({ commit }, val) {
    commit('setNoPriorAccessAlert', val)
  },
  async loadReservation ({ commit, dispatch, rootState }) {

    const reservation = await appsync.retrieveReservation(rootState.amplify.user.username)
    // need to show error dialog here
    if (!reservation) {
      // need to show error dialog here
      // console.error('No Reservation Available')
      throw new Error('No Reservation Available!');
    } else {
      commit('setReservation', reservation)
    }

    // await dispatch('reloadGuests')
    commit('setCurrentStep', 1)

    return

  },
  async reloadGuests ({ commit, getters, state, rootState }) {

    const guests = await appsync.retrieveGuests(rootState.amplify.user.username)
    // console.log('reloadGuests', guests)

    if (guests.length < state.reservation.guestCount) {
      let i = guests.length
      console.log('i:', i)
      for (i; i < state.reservation.guestCount; i++) {
        console.log('i:', i)
        guests.push({ 
          reservationCode: rootState.amplify.user.username, 
          guestNo: i, 
          firstName: ' ', 
          lastName: ' '
        })
      }
    }

    commit('setGuests', guests)

  },
/*
  async inputGuest ({ commit, getters, state }, val) {

    console.log('val', val)


    const guests = state.guests.slice(0)
    guests.splice(state.currentStep - 1, 1, val)
    commit('setGuests', guests)
    // console.log('inputGuest this.guest', getters.guest)

  },
*/
  async saveGuest ({ commit, getters }) {
    if (getters.guest.lastUpdateOn) {
      getters.guest.lastUpdateOn = (new Date).toISOString()
      return await appsync.updateReservationGuest(getters.guest)
    } else {
      getters.guest.lastUpdateOn = (new Date).toISOString()
      return await appsync.createReservationGuest(getters.guest)
    }
  },
  async saveReservation ({ commit, getters, state }) {
    const anyNotPassed = state.guests.some(crtGuest => !crtGuest.passed)

    if (!anyNotPassed) {
      state.reservation.lastUpdateOn = (new Date).toISOString()
      state.reservation.checkInDone = true
      return await appsync.updateReservation(state.reservation)
    }
    
  },
  async putImage ({ commit, getters, state, rootState}, { img, isId }) {
    getters.guest.imgBucket = aws_exports.aws_user_files_s3_bucket
    getters.guest.passed = false
    
    if (isId) {
      getters.guest.idImgName = `id/${state.currentStep - 1}.jpg`
      getters.guest.idImgKey = `private/${rootState.amplify.userIdentityId}/id/${state.currentStep - 1}.jpg`

      await storage.put(getters.guest.idImgName, img.split(',')[1])
    } else {
      getters.guest.faceImgName = `face/${state.currentStep - 1}.jpg`
      getters.guest.faceImgKey = `private/${rootState.amplify.userIdentityId}/face/${state.currentStep - 1}.jpg`

      await storage.put(getters.guest.faceImgName, img.split(',')[1])
    }
  },

  setCurrentStep ({ commit, dispatch }, val) {
    commit('setCurrentStep', val)
    dispatch('setIdImgSrc')
    dispatch('setFaceImgSrc')

  },
  async setIdImgSrc ({ commit, getters, state }) {
    if (getters.guest) {
      if (getters.guest.idImgName) {
        const url = await storage.get(getters.guest.idImgName)
        commit('setIdImgSrc', url)
      } else {
        commit('setIdImgSrc', require('@/assets/img/id.svg'))
      }        
    } else {
      commit('setIdImgSrc', require('@/assets/img/id.svg'))
    }
  },
  async setFaceImgSrc ({ commit, getters }) {
    if (getters.guest) {
      if (getters.guest.faceImgName) {
        const url = await storage.get(getters.guest.faceImgName)
        commit('setFaceImgSrc', url)
      } else {
        commit('setFaceImgSrc', require('@/assets/img/face.svg'))
      }
    } else {
      commit('setFaceImgSrc', require('@/assets/img/face.svg'))
    }
  },
  async verifyId ({ commit, getters}) {
    const res = await appsync.compareFaces({
      imgBucket: getters.guest.imgBucket,
      idImgKey: getters.guest.idImgKey,
      faceImgKey: getters.guest.faceImgKey,
      similarityThreshold: 50
    })

    getters.guest.passed = res.passed
    getters.guest.similarity = res.similarity
    getters.guest.messageCode = res.messageCode
  }
}

const getters = {
    reservation: state => {
      return state.reservation
    },
    steps: state => {
      return state.guests.length
    },
    guest: state => {
      return state.guests[state.currentStep - 1]
    },
    currentStep: state => {
      return state.currentStep
    },
    idImgSrc: state => {
      return state.idImgSrc
    },
    faceImgSrc: state => {
      return state.faceImgSrc
    },
    noPriorAccessAlert: state => {
      return state.noPriorAccessAlert
    }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}