<template>
  <v-layout row justify-center>
    <v-dialog v-model="showDialog" persistent max-width="290">
      <v-card>
        <v-card-title class="headline">Use Google's location service?</v-card-title>
        <v-card-text>Let Google help apps determine location. This means sending anonymous location data to Google, even when no apps are running.</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat @click="showDialog = false">Disagree</v-btn>
          <v-btn color="green darken-1" flat @click="showDialog = false">Agree</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import { EventBus } from '@/eventBus'

export default {
  name: 'ModalDialog',
  data () {
    return {
      showDialog: false
    }
  },
  mounted () {
    let vm = this
    EventBus.$on('ModalDialog', function (value) {
      vm.showDialog = value.showDialog
    })
  }
}
</script>